/* ----------------------------------------------------------------
    Promo Boxes
-----------------------------------------------------------------*/
.promo {
  position: relative;
  padding: 30px 200px 30px 0;
}

.promo h3 {
  font-weight: bold;
  margin-bottom: 0;
}

.promo a.button {
  position: absolute;
  top: 50%;
  margin: 0;
  margin-top: -18px;
  left: auto;
  right: 0;
}

.promo > span,
.promo > .container > span {
  display: block;
  color: #444;
  font-weight: 300;
  font-size: 16px;
  margin-top: 6px;
}

.promo a.button.button-mini {
  margin-top: -12px;
}

.promo a.button.button-small {
  margin-top: -15px;
}

.promo a.button.button-large {
  margin-top: -22px;
}

.promo a.button.button-xlarge {
  margin-top: -25px;
}

.promo a.button.button-desc {
  margin-top: -40px;
}

/* Promo Uppercase
-----------------------------------------------------------------*/
.promo-uppercase {
  text-transform: uppercase;
}

.promo-uppercase h3 {
  font-size: 22px;
}

.promo-uppercase > span,
.promo-uppercase > .container > span {
  font-size: 15px;
}

/* Promo - with Border
-----------------------------------------------------------------*/
.promo.promo-border {
  border: 1px solid #E5E5E5;
  border-radius: 3px;
  padding-left: 30px;
}

.promo.promo-border a.button {
  right: 30px;
}

/* Promo - Light
-----------------------------------------------------------------*/
.promo.promo-light {
  background-color: #F5F5F5;
  border-radius: 3px;
  padding-left: 30px;
}

.promo.promo-light a.button {
  right: 30px;
}

/* Promo - Dark
-----------------------------------------------------------------*/
.promo.promo-dark {
  border: 0 !important;
  background-color: #333;
  padding: 30px 200px 30px 30px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
