/* ----------------------------------------------------------------
    Icon Overlay
-----------------------------------------------------------------*/
.i-overlay,
.i-overlay a {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-align: center;
  background: rgba(0, 0, 0, 0.2) url("images/grid.png") repeat;
}

.i-overlay a {
  background: transparent;
}

.i-overlay img,
.i-overlay i {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -24px 0 0 -24px;
  width: 48px !important;
  height: 48px !important;
  font-size: 48px;
  line-height: 1;
  color: #E5E5E5;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
}

.i-overlay i {
  margin: -18px 0 0 -18px;
  width: 36px !important;
  height: 36px !important;
  line-height: 36px;
  font-size: 24px;
}

.on-hover.i-overlay {
  opacity: 0;
}

.on-hover.i-overlay:hover {
  opacity: 1;
}

body:not(.device-touch) .on-hover.i-overlay {
  transition: opacity 0.3s linear;
}

/* ----------------------------------------------------------------
    Social Icons
-----------------------------------------------------------------*/
.social-icon {
  margin: 0 5px 5px 0;
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 38px !important;
  color: #555;
  text-shadow: none;
  border: 1px solid #AAA;
  border-radius: 3px;
  overflow: hidden;
}

/* Social Icons - Rounded
-----------------------------------------------------------------*/
.si-rounded {
  border-radius: 50%;
}

/* Social Icons - Borderless
-----------------------------------------------------------------*/
.si-borderless {
  border-color: transparent !important;
}

/* Social Icons - Dark
-----------------------------------------------------------------*/
.si-dark {
  background-color: #444;
  color: #FFF !important;
  border-color: transparent;
}

/* Social Icons - Light
-----------------------------------------------------------------*/
.si-light {
  background-color: rgba(0, 0, 0, 0.05);
  color: #666 !important;
  border-color: transparent;
}

/* Social Icons - Colored
-----------------------------------------------------------------*/
.si-colored {
  border-color: transparent !important;
}

.si-colored i {
  color: #FFF;
}

/* Social Icons - Large
-----------------------------------------------------------------*/
.social-icon.si-large {
  margin: 0 10px 10px 0;
  width: 56px;
  height: 56px;
  font-size: 30px;
  line-height: 54px !important;
}

/* Social Icons - Small
-----------------------------------------------------------------*/
.social-icon.si-small {
  width: 32px;
  height: 32px;
  font-size: 14px;
  line-height: 30px !important;
}

.social-icon i {
  display: block;
  position: relative;
}

body:not(.device-touch) .social-icon i {
  transition: all 0.3s ease;
}

.social-icon i:last-child {
  color: #FFF !important;
}

.social-icon:hover {
  color: #555;
  border-color: transparent;
}

.social-icon:hover i:first-child {
  margin-top: -38px;
}

.social-icon.si-large:hover i:first-child {
  margin-top: -54px;
}

.social-icon.si-small:hover i:first-child {
  margin-top: -30px;
}

.si-colored.social-icon {
  border-color: transparent;
}

/* Social Icons - Sticky
-----------------------------------------------------------------*/
.si-sticky {
  position: fixed;
  top: 50%;
  left: 5px;
  width: 36px;
  z-index: 100;
}

/* Social Icons - Sticky Right
-----------------------------------------------------------------*/
.si-sticky.si-sticky-right {
  left: auto;
  right: 8px;
}

