/* ----------------------------------------------------------------
    Overlays
-----------------------------------------------------------------*/
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.overlay .overlay-wrap,
.text-overlay {
  opacity: 0;
